"use strict";
var docsPages = require('./pages');
var mapping = {};
mapping['*'] = {
    component: {
        template: '<h1>Not Found</h1>'
    }
};
docsPages.forEach(function (page) {
    page.urls.forEach(function (url) {
        mapping[url] = {
            component: page.component,
            title: page.title
        };
    });
});
module.exports = mapping;
